 /* Base styles for navbar */
.navbar {
  background-color: #333;
  color: white;
  padding: 10px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

.frame-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.frame-group {
  display: flex;
  gap: 60px;
}

.kembang-ayu-wrapper {
  cursor: pointer;
}

.navbar-toggle {
  display: none; /* Hidden by default */
  cursor: pointer;
  font-size: 24px;
  color: white;
}

.navbar-toggle-icon {
  display: inline-block;
  font-size: 24px;
}

