
.register-child {
  position: absolute;
  top: 135px;
  left: 160px;
  box-shadow: 2px 4px 22.4px -3px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-sm);
  background-color: var(--putih);
  border: 1px solid var(--color-darkgray-100);
  box-sizing: border-box;
  width: 959px;
  height: 562px;
  margin-left: 315px;
}
.vector-icon1 {
  width: 19px;
  position: relative;
  height: 19px;
}
.vector {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-11xs);
  
}
.nomor-telepon {
  position: relative;
  font-weight: 600;
  border: none;
  outline: none;
  
}
.frame-div {
  position: absolute;
  top: 458px;
  left: 425px;
  box-shadow: 0 0 2.2px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-9xs);
  background-color: var(--color-gray-100);
  border: 1px solid var(--color-gainsboro);
  box-sizing: border-box;
  width: 430px;
  height: 52px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-mini);
  gap: var(--gap-mini);
  margin-left: 320px;
}
.vector-icon2 {
  width: 22px;
  position: relative;
  height: 17px;
}
.vector-parent3,
.vector1 {
  width: 27px;
  height: 26px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: var(--padding-11xs);
  box-sizing: border-box;
}
.vector-parent3 {
  position: absolute;
  top: 388px;
  left: 425px;
  box-shadow: 0 0 2.2px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-9xs);
  background-color: var(--color-gray-100);
  border: 1px solid var(--color-gainsboro);
  width: 430px;
  height: 52px;
  flex-direction: row;
  justify-content: flex-start;
  padding: 0 var(--padding-mini);
  gap: var(--gap-mini);
  margin-left: 320px;
}
.register1 {
  position: relative;
  cursor: pointer;
  color: white;
  background-color: transparent;
  width: 430px;
  height: 52px;
  border: none;
}
.register-wrapper {
  position: absolute;
  top: 526px;
  left: calc(50% - 215px);
  box-shadow: 0 0 2.2px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-4xs);
  background: linear-gradient(139.41deg, #c9a180, #3b2410);
  border: 1px solid var(--color-gainsboro);
  box-sizing: border-box;
  width: 430px;
  height: 52px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-mini);
  font-size: var(--font-size-xl);
  color: var(--putih);
}
.register2 {
  position: absolute;
  top: 189px;
  left: calc(50% - 96px);
  font-size: var(--font-size-11xl);
  font-weight: 800;
  color: var(--biru-tua);
  text-align: center;
  display: inline-block;
  width: 192px;
}
.user-round-icon1 {
  width: 28px;
  position: relative;
  height: 28px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: contain;
}
.user-round-group {
  position: absolute;
  top: 248px;
  left: calc(50% - 215px);
  box-shadow: 0 0 2.2px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-9xs);
  background-color: var(--color-gray-100);
  border: 1px solid var(--color-gainsboro);
  box-sizing: border-box;
  width: 430px;
  height: 52px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-mini);
  gap: var(--gap-mini);
}
.vector-icon3 {
  width: 18px;
  position: relative;
  height: 20px;
}
.instance-container,
.vector-frame {
  width: 26px;
  height: 28px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-11xs);
  box-sizing: border-box;
}
.instance-container {
  position: absolute;
  top: 318px;
  left: calc(50% - 215px);
  box-shadow: 0 0 2.2px rgba(0, 0, 0, 0.25);
  border-radius: var(--br-9xs);
  background-color: var(--color-gray-100);
  border: 1px solid var(--color-gainsboro);
  width: 430px;
  height: 52px;
  justify-content: flex-start;
  padding: 0 var(--padding-mini);
  gap: var(--gap-mini);
}
.have-an-account-container {
  position: absolute;
  top: 595px;
  left: calc(50% + 35px);
  font-size: var(--font-size-xs);
  color: var(--biru-tua);
  cursor: pointer;
}
.kembang-ayu7 {
  position: relative;
  background: linear-gradient(139.41deg, #c9a180, #3b2410);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 320px;
  font-size: 2rem; 
}
.kembang-ayu-wrapper1 {
  position: absolute;
  top: 142px;
  left: 181px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-4xs-9);
  font-size: var(--font-size-13xl);
  font-family: var(--font-dancing-script);
}
.register {
  width: 100%;
  position: relative;
  background-color: var(--color-6);
  height: 832px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-darkslateblue-100);
  font-family: var(--font-nunito-sans);
}
.success-popup {
  position: fixed;
  font-size: 20px;
  top: 300px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(139.41deg, #4e2e13, #fba045);
  color: white;
  padding: 15px;
  border-radius: 5px;
  z-index: 1000;
  text-align: center;
}

/* Styles for very small devices (max-width: 480px) */
@media (max-width: 480px) {
.register-child {
  width: 380px;
  height: 550px;
  top: 14%;
  left: -309px;
}
.register-wrapper,
.user-round-group,
.vector-parent3,
.instance-container,
.frame-div {
    width: 85%;
    left: 7.5%;
    margin-left: 0;
}
.register2 {
    font-size: var(--font-size-4xl);
    width: 100%;
    left: -5px;
}
.have-an-account-container {
    top: 71%;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    font-size: 13px;
}
.kembang-ayu7 {
    margin-left: 0;
    font-size: 2rem;
    text-align: center; 
    top: 80px;
}
.kembang-ayu-wrapper1 {
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    width: 90%;
    text-align: center;
}
}
